import React, { useState } from 'react';
import { useMutation, useQuery } from 'react-query';
import { authMailPost, emailConfirmGet, nicknameConfirmGet, signupPost, verifyMailPost } from '../api/users';
import { useNavigate } from 'react-router-dom';
import { majorGet } from '../api/certificate';
import { styled } from 'styled-components';
import CustomBtn from '../components/common/CustomBtn';
import CustomText from '../components/common/CustomText';

function Signup() {
  const navigate = useNavigate();
  // 
  const { data } = useQuery('major', majorGet);

  //input  
  const [email, setEmail] = useState('');
  const [nickname, setNickname] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPw, setConfirmPw] = useState('');
  const [major_id, setMajor_id] = useState('');
  const [verifyCode, setVerifyCode] = useState('');

  // 
  const [emailMsg, setEmailMsg] = useState('');
  const [nicknameMsg, setNicknameMsg] = useState('');
  const [passwordMsg, setPasswordMsg] = useState('');
  const [confirmPwMsg, setConfirmPwMsg] = useState('');

  //
  const [isEmail, setIsEmail] = useState(false);
  const [isNickname, setIsNickname] = useState(false);
  const [isPassword, setIsPassword] = useState(false);
  const [isConfirmPw, setIsConfirmPw] = useState(false);
  const [isMajor, setIsMajor] = useState(false);

  //
  const [existsEmail, setExistsEmail] = useState(false);
  const [existsNickname, setExistsNickname] = useState(false);

  // 
  const [verifyEmail, setVerifyEmail] = useState(false);

  //        
  const goLogin = () => {
    navigate('/logins');
  };

  //   
  const onChangeEmailHandler = (e) => {
    const emailRegex = /[a-zA-Z0-9]+[@][a-zA-Z0-9]+[.]+[a-zA-Z]+[.]*[a-zA-Z]*/;
    const emailCurrent = e.target.value;
    setEmail(emailCurrent);

    if (!emailRegex.test(emailCurrent)) {
      setEmailMsg('  .  .');
      setIsEmail(false);
    } else {
      setEmailMsg('  .');
      setIsEmail(true);
    }
  };

  //  
  const onChangeNicknameHandler = (e) => {
    const nicknameRegex = /^[a-zA-Z0-9-]{2,10}$/;
    const nicknameCurrent = e.target.value;
    setNickname(nicknameCurrent);

    if (!nicknameRegex.test(nicknameCurrent)) {
      setNicknameMsg(' , ,  2  10  .');
      setIsNickname(false);
    } else {
      setNicknameMsg('  .');
      setIsNickname(true);
    }
  };

  //  
  const onChangePasswordHandler = (e) => {
    const passwordRegex = /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[@$!%*?&])[a-zA-Z\d@$!%*?&]{8,15}$/;
    const passwordCurrent = e.target.value;
    setPassword(passwordCurrent);

    if (!passwordRegex.test(passwordCurrent)) {
      setPasswordMsg(' , ,    8  15 .');
      setIsPassword(false);
    } else {
      setPasswordMsg('  .');
      setIsPassword(true);
    }
  };

  //   
  const onChangeConfirmPwHandler = (e) => {
    const confirmPwCurrent = e.target.value;
    const passwordCurrent = password;
    setConfirmPw(confirmPwCurrent);

    if (confirmPwCurrent !== passwordCurrent) {
      setConfirmPwMsg(' .  .');
      setIsConfirmPw(false);
    } else {
      setConfirmPwMsg(' .');
      setIsConfirmPw(true);
    }
  };

  //  
  const onChangeMajorHandler = (e) => {
    const majorCurrent = e.target.value;
    setMajor_id(majorCurrent);

    if (majorCurrent === '') {
      setIsMajor(false);
    } else {
      setIsMajor(true);
    }
  };

  //  
  const onChangeVerifyCodeHandler = (e) => {
    setVerifyCode(e.target.value);
  };

  //    
  const authEmaliMutation = useMutation(authMailPost, {
    onSuccess: () => {
      setExistsEmail(true);
    },
  });

  //   
  const emailConfirmClick = async () => {
    const response = await emailConfirmGet(email);
    console.log(response);
    if (response === 200) {
      //     
      authEmaliMutation.mutate({ email });
    }
  };

  //   
  const nicknameConfirmHandler = async () => {
    const response = await nicknameConfirmGet(nickname);
    if (response === 200) {
      setExistsNickname(true);
    }
  };

  //   
  const verifyMailMutation = useMutation(verifyMailPost, {
    onSuccess: () => {
      setVerifyEmail(true);
    },
  });

  const verifyMailClick = () => {
    const verify = {
      email,
      userCode: verifyCode,
    };
    verifyMailMutation.mutate(verify);
  };

  //  
  const signupMutation = useMutation(signupPost, {
    onSuccess: () => {
      goLogin();
    },
  });
  const submitHandler = (e) => {
    e.preventDefault();
    const newUser = {
      email,
      nickname,
      password,
      major_id,
    };
    signupMutation.mutate(newUser);
  };

  return (
    <form onSubmit={submitHandler}>
      <SignupLayout>
        <SignupContainer>
          {/*   */}
          <TitleBox>
            <CustomText color='#000' fontSize='2rem' fontWeight='700'>
              
            </CustomText>
            <CustomText
              color='#282897'
              fontSize='1.5rem'
              fontWeight='700'
              textDecoration='underline'
              cursor='pointer'
              onClick={goLogin}>
              
            </CustomText>
          </TitleBox>

          {/*  */}
          <ItemBox>
            <CustomText fontSize='1.5rem'></CustomText>
            <ConfirmBox>
              <SignupInput
                name='email'
                onChange={onChangeEmailHandler}
                value={email}
                type='email'
                placeholder='email@gamil.com'
                pattern='[a-zA-Z0-9]+[@][a-zA-Z0-9]+[.]+[a-zA-Z]+[.]*[a-zA-Z]*'
              />

              <CustomBtn
                type='button'
                width='200px'
                height='60px'
                bc='#282897'
                _borderradius='10px'
                onClick={emailConfirmClick}>
                <CustomText color='#fff' fontSize='1.2rem' fontWeight='600'>
                  
                </CustomText>
              </CustomBtn>
            </ConfirmBox>

            {email.length > 0 && <ErrorMsg iserror={isEmail.toString()}>{emailMsg}</ErrorMsg>}
          </ItemBox>

          {/*  */}
          <ItemBox>
            <ConfirmBox>
              <SignupInput
                name='verifyCode'
                onChange={onChangeVerifyCodeHandler}
                value={verifyCode}
                placeholder=' .'
              />

              <CustomBtn
                type='button'
                width='200px'
                height='60px'
                bc='#282897'
                _borderradius='10px'
                onClick={verifyMailClick}>
                <CustomText color='#fff' fontSize='1.2rem' fontWeight='600'>
                  
                </CustomText>
              </CustomBtn>
            </ConfirmBox>
          </ItemBox>

          {/*  */}
          <ItemBox>
            <CustomText fontSize='1.5rem'></CustomText>
            <SignupInput
              name='password'
              onChange={onChangePasswordHandler}
              value={password}
              type='password'
              placeholder=''
            />

            {password.length > 0 && <ErrorMsg iserror={isPassword.toString()}>{passwordMsg}</ErrorMsg>}
          </ItemBox>

          {/*   */}
          <ItemBox>
            <CustomText fontSize='1.5rem'> </CustomText>
            <SignupInput
              name='confirmPw'
              onChange={onChangeConfirmPwHandler}
              value={confirmPw}
              type='password'
              placeholder=' '
            />

            {confirmPw.length > 0 && <ErrorMsg iserror={isConfirmPw.toString()}>{confirmPwMsg}</ErrorMsg>}
          </ItemBox>

          {/*  */}
          <ItemBox>
            <CustomText fontSize='1.5rem'></CustomText>
            <ConfirmBox>
              <SignupInput
                name='nickname'
                onChange={onChangeNicknameHandler}
                value={nickname}
                type='text'
                placeholder=''
              />

              <CustomBtn
                type='button'
                width='200px'
                height='60px'
                bc='#282897'
                _borderradius='10px'
                onClick={nicknameConfirmHandler}>
                <CustomText color='#fff' fontSize='1.2rem' fontWeight='600'>
                  
                </CustomText>
              </CustomBtn>
            </ConfirmBox>

            {nickname.length > 0 && <ErrorMsg iserror={isNickname.toString()}>{nicknameMsg}</ErrorMsg>}
          </ItemBox>

          {/* */}
          <ItemBox>
            <CustomText fontSize='1.5rem'></CustomText>
            <MajorSelector name='major_id' value={major_id} onChange={onChangeMajorHandler}>
              <option value='' disabled hidden>
                 
              </option>
              {data?.map((major) => {
                return (
                  <option key={major.major_id} value={major.major_id}>
                    {major.name}
                  </option>
                );
              })}
            </MajorSelector>

            {major_id === '' && <ErrorMsg> </ErrorMsg>}
          </ItemBox>

          <CustomBtn
            _borderradius='10px'
            margin='78px 0 0 0'
            width='100%'
            height='72px'
            bc='#282897'
            type='submit'
            disabled={
              !isEmail ||
              !isNickname ||
              !isPassword ||
              !isMajor ||
              !isConfirmPw ||
              !existsEmail ||
              !existsNickname ||
              !verifyEmail
            }>
            <CustomText color='#fff' fontSize='1.5rem' fontWeight='700'>
              
            </CustomText>
          </CustomBtn>
        </SignupContainer>
      </SignupLayout>
    </form>
  );
}

const SignupLayout = styled.div`
  width: 588px;
  margin: auto;
`;

const SignupContainer = styled.div`
  width: 588px;

  margin: 70px 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const TitleBox = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;

  margin-bottom: 40px;

  align-items: baseline;
  justify-content: flex-end;
  gap: 156px;
`;

const SignupInput = styled.input`
  width: 100%;
  height: 60px;

  border-radius: 10px;
  border: none;

  box-sizing: border-box;

  padding-left: 25px;

  background: #eff3fd;

  font-size: 1.2rem;

  &::placeholder {
    font-size: 20px;
    color: #898989;
  }
`;

const MajorSelector = styled.select`
  width: 100%;
  height: 60px;

  border-radius: 10px;
  border: none;

  box-sizing: border-box;

  background: #eff3fd;

  text-align: center;

  color: #898989;

  font-size: 20px;
`;

const ItemBox = styled.div`
  width: 100%;
  margin: 12px 0;

  display: flex;
  flex-direction: column;

  align-items: start;
  justify-content: center;

  gap: 5px;
`;

const ConfirmBox = styled.div`
  width: 100%;

  display: flex;
  flex-direction: row;

  align-items: center;
  justify-content: end;

  gap: 15px;
`;

const ErrorMsg = styled.span`
  color: ${({ iserror }) => (iserror === 'true' ? 'blue' : 'red')};
`;
export default Signup;
